import { useState, useEffect } from "react";
import { useApp } from "../AppProvider";
import {MetrixType} from './components/Metrix'
import {H1, H2, H3} from './components/Headers'
import {printDate, getTime} from '../lib/indexing';
import CancelSVG from '../img/cancel.svg'
import TrashSVG from '../img/trash.svg'

const defualtPrompts = [
  {
    id: 'default-0',
    unit: 'string',
    prompt: 'What have I done today?',
    frequency: 'as needed'
  },
  {
    id: 'default-1',
    unit: 'string',
    prompt: 'How do I feel?',
    frequency: 'as needed'
  },
  {
    id: 'default-2',
    unit: 'string',
    prompt: 'What else am I going to do today?',
    frequency: 'as needed'
  }
];

export default function Write () {
  // state stuff
  const {
    auth: {user},
    router: {page, redirect},
    journal: {createEntry},
    settings: {getSetting, toggle, set},
    writing: {questions, recordingStatus, resetAudioInformation},
    audio: {audioRecording, audioRecordingTitle},
    metrix: {metrix, createMeasurements, measure, isCaptured},
    session,
    freeze
  } = useApp ();
  const [start, setStart] = useState (new Date ());
  const [prompts, setPrompts] = useState (defualtPrompts)
  const [isSelecting, setIsSelecting] = useState ('');

  // create entry
  const submit = async () => {
    let unfreeze = freeze ();
    try {
      const qa = session.getAll ();
      const body = {
        entryType: 'questions',
        start,
        end: new Date (),
        questions: Object.keys (qa),
        answers: Object.values (qa)
      }
      await createEntry (body);
      await createMeasurements ();
      unfreeze ();
    } catch (e) {
      unfreeze ();
    }
  }

  const onChange = (prompt, value) => {
    measure (prompt, value);
    session.set (prompt.prompt, value);
  }

  const removePrompt = (prompt) => {
    setPrompts (prompts.filter (p => p.id !== prompt.id))
    measure (prompt, undefined);
    session.set (prompt.prompt, undefined);
  }
  // when metrix is redefined update
  useEffect (() => {
    let rawPrompts = session.getAll ();
    let _ps = Object.keys (rawPrompts);
    let _prompts = _ps.map (p => {
      let found = defualtPrompts.find (_p => _p.prompt === p);
      console.log (found);
      if (found) return found;
      return metrix.find (_p => _p.prompt === p);
    }).filter (a => a);
    setPrompts (_prompts);
  }, [metrix]);
  // only render if authenticated & on write page
  if (!user || page !== 'write') return null;
  return (
    <main id="question-answer-entry">
      <H1 short={'New Entry'}>{'New Question & Answer Entry'}</H1>
      <H2>{printDate ()}</H2>
      <H3>{getTime (start)}</H3>
      <div className="questions">
        {
          prompts.map (prompt => {
            const Metric = MetrixType [prompt.unit];
            return (
              <>
                <span className="prompt-prompt">
                  <b>{prompt.prompt}</b>
                  <span onClick={() => removePrompt (prompt)}><img src={TrashSVG} /></span>
                </span>
                <Metric onChange={value => onChange (prompt, value)} defaultValue={session.getSetting (prompt.prompt)} {...prompt} />
                <hr className="prompt-divider" />
              </>
            )
          })
        }
        {
          [...metrix, ...defualtPrompts].filter (m => prompts.find (p => p.id === m.id)) !== 0 &&
          <>
            <section onClick={() => setIsSelecting ('selecting')} className={`add-a-section ${isSelecting}`}>+</section>
            <section>
              {
                isSelecting === 'selecting' &&
                <select autoFocus onChange={e => e.target.value ? (setPrompts ([...prompts, [...metrix, ...defualtPrompts].find (m => e.target.value === m.id)]) || setIsSelecting ('')) : setIsSelecting ('')}>
                  <option value={null} defaultChecked></option>
                  {
                    [...metrix, ...defualtPrompts].filter (m => !isCaptured (m)).filter (m => !prompts.find (_m => m.id === _m.id)).map (metric => (
                      <option value={metric.id}>{metric.prompt}</option>
                    ))
                  }
                </select>
              }
              <span className="fake-button" onClick={() => setIsSelecting ('')}><img src={CancelSVG} /></span>
            </section>
          </>
        }
      </div>
      <button onClick={submit}>Submit</button>
    </main>
  )
}